.Arbeitsplane{height:100%;border-top:2px solid #3a3939}.Arbeitsplane__content{display:flex;height:100%}.Arbeitsplane__content_left{padding-top:50px;border-right:2px solid #3a3939}.Arbeitsplane__content_right{flex-grow:1;padding:50px 0px 0px 30px;display:flex;flex-direction:column;justify-content:space-between}.employees{margin-right:100px}.employees__body li.item{padding:5px 20px;position:relative}.employees__body li.item::before{content:"";display:block;position:absolute;width:10px;height:10px;background-color:#0bac0b;left:0;border-radius:50%;top:50%;transform:translateY(-50%)}.list__list li.item{padding:7px 0px 7px 30px;position:relative}.list__list li.item.check::before{content:"✔";font-size:14px;color:#fff;background-color:#0bac0b;width:20px;height:20px;display:flex;align-items:center;justify-content:center;position:absolute;left:0;top:50%;transform:translateY(-50%);border-radius:50%}.list__list li.item.cross::before{content:"✖";font-size:14px;color:red;border:3px solid red;width:20px;height:20px;box-sizing:border-box;display:flex;align-items:center;justify-content:center;position:absolute;left:0;top:50%;transform:translateY(-50%);border-radius:3px}.data{display:flex;justify-content:end}.data__container{display:flex;align-items:center}.data__container_text{font-size:18px;margin-right:10px}.data__container_select{width:15vw}.data__container_select input{cursor:pointer;text-align:center;font-size:16px;width:100%;padding:5px}.btns{display:flex;justify-content:end;margin-bottom:20px}.btns__item{margin-left:10px}